.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* App.css */

body {
  font-family: Arial;
  background-color:black;
  color: white;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 1000px;
  margin: 20px auto;
  background:orange;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(182, 81, 18, 0.1);
}

h1{
  text-align: center;
}

h1, h2 {
  color:white;
  margin: 40px 0px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

li:last-child {
  border-bottom: none;
}

button {
  padding: 5px 10px;
  margin: 0 5px;
  background-color: #dd3737;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #7f0000;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.total {
  text-align: right;
  font-weight: bold;
  font-size: 1.2em;
}

.shop-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.shop-item h2 {
  margin: 0;
}

.shop-item button {
  margin-left: 20px;
}
